export const validMail = (mail) =>
{
    return /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()\.,;\s@\"]+\.{0,1})+([^<>()\.,;:\s@\"]{2,}|[\d\.]+))$/.test(mail);
}

export const validPhone = (phone) =>
{
    return /^[+]{1}996[0-9]{9}$/.test(phone);
}

export const getPrice = (item) =>
{
    let price = ''
    if(store.getState().app.currency==='USD'){
        price = item.prices + ' $'
    } else if(store.getState().app.currency==='RUB'){
        price = (parseInt(item.prices) * parseInt(store.getState().app.currencyCourse[store.getState().app.currency])) + ' руб'
    } else if(store.getState().app.currency==='KGS'){
        price = (parseInt(item.prices) * parseInt(store.getState().app.currencyCourse[store.getState().app.currency])) + ' сом'
    }
    return price;
}

